.App {
  text-align: center;
  height: 100%;
  width: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.container{
  /* 전체 담는 통 */
  width: 597px;
  height: auto;
  margin:  100px auto 0;
}
.jongro{
  position: absolute;
  top: 160px;
  left: 415px;
  width:170px;
  height:auto;
  object-fit: cover;
}
.junggu{
  position: absolute;
  top: 308px;
  left: 435px;

  width:150px;
  height:auto;
  object-fit: cover;
}
.yongsan{
  position: absolute;
  top: 349px;
  left: 400px;

  width:170px;
  height:auto;
  object-fit: cover;
}
/* .Img_gu{
  position:absolute;
  top: 0;
  left: 0;
} */
.Img_seoul {
  /* 뒤에 */
  position: absolute;
  top:0;
  left:0;
}  

.Img_gu img {
  transition: all 0.2s linear;
}

.Img_gu:hover img {
  z-index: 1;
  transform: scale(1.4);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
